<template>
  <div class='creatOrder'>
    <h1 class="orderTitle">订购页面</h1>
    <div class="orderList">
      <ul>
        <li v-for="(item,index) in orderList" :key="'order'+index" :class="{'orderFocus':itemNo===index&&pos===0}">
          <div class="orderDetail">
            <div class="orderName">
              <p>{{item.name}}</p>
            </div>
          </div>
          <div class="orderPrice">
            <p>{{item.price}}元/月</p>
          </div>
          <div class="orderTime">
            <p>(有效期:{{item.expirydata}})</p>
          </div>
        </li>
      </ul>
    </div>
    <div :class="{'orderButtonFocus':pos ===1,'orderBottom':true}">
      <span>暂不订购</span>
      <i></i>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>
<style scoped>
  .creatOrder{
    position: absolute;
    width: 1280px;
    height: 720px;
    background: url('../../assets/payfor/orderBg.png') no-repeat;
    background-size: 100% 100%;
  }
  .orderTitle{
    position: absolute;
    color: #AC9065;
    font-size: 41px;
    top: 40px;
    left: 52px;
    width: 164px;
    height: 57px;
    text-align: center;
    font-weight: 400;
  }
  .orderList{
    position: absolute;
    top: 250px;
    left: 180px;
    width: 950px;
    height: 175px;
  }
  .orderList li{
    list-style: none;
    float: left;
    width: 270px;
    height: 170px;
    margin-right: 33px;
    border-radius: 20px;
    background: url('../../assets/payfor/order_init.png') no-repeat;
    background-size: 100% 100%;
  }
  .orderFocus{
    background: url('../../assets/payfor/orderFocus.png') no-repeat !important;
    background-size: 100% 100%;
    box-shadow: black 10px 10px 10px;
  }
  .orderName{
    position: relative;
    width: 100%;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-size: 35px;
    font-weight: bolder;
  }
  .orderPrice{
    position: relative;
    width: 100%;
    height: 50px;
    font-size: 30px;
    line-height: 50px;
    text-align: center;
    color: #FFB43B;
    letter-spacing: 2px;
  }
  .orderTime{
    position: relative;
    width: 100%;
    height: 50px;
    font-size: 25px;
    text-align: center;
    line-height: 25px;
    color: #FFB43B;
    letter-spacing: 3px;
  }
  .orderBottom{
    position: absolute;
    top: 624px;
    left: 42px;
    width: 207px;
    height: 60px;
    background: linear-gradient(
    to left,
    rgba(255, 255, 255, 0.3) 0%,
    rgba(255, 255, 255, 0.3) 50%,
    rgba(255, 255, 255, 0.3) 100%
    );
    border-radius: 15px;
  }
  .orderButtonFocus{
    background: #0098BC;
    border: 2px solid #FFFFFF;
  }
  .orderBottom span{
    position: absolute;
    width: 130px;
    height: 100%;
    padding-left: 20px;
    text-align: left;
    line-height: 58px;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 2px;
  }
  .orderBottom i{
    position: absolute;
    width: 25px;
    height: 25px;
    left: 150px;
    top: 17px;
    background: url('../../assets/payfor/close.png') no-repeat;
    background-size: 100% 100%;
  }
</style>
<script>
import {bi} from '../../js/logApi.js'
import c from '../../js/common.js'
import {auth} from '../../js/auth.js'
import { mapGetters } from 'vuex';
export default {
  props: ['detailData', 'movieUrl'],
  data (){
    return {
      orderLists: [
        {name: '赛驰电竞', price: '18', expirydata: '一个月', productType: 1, id: 0, productid: '1018000003'},
        {name: '小鬼当家', price: '20', expirydata: '一个月', productType: 1, id: 1, productid: '1018000004'},
        {name: '3元单点', price: '3', expirydata: '2天', productType: 0, id: 2, productid: 's100000849'},
        {name: '5元单点', price: '5', expirydata: '2天', productType: 0, id: 3, productid: 's100001127'},
        {name: '8元单点', price: '8', expirydata: '2天', productType: 0, id: 4, productid: 's100000851'},
        {name: '1元单点', price: '1', expirydata: '2天', productType: 0, id: 5, productid: 's100000707'},
        {name: '赛驰电竞促销', price: '9', expirydata: '一个月', productType: 1, id: 6, productid: '10180000042'},
        {name: '小鬼当家促销', price: '10', expirydata: '一个月', productType: 1, id: 7, productid: '10180000040'}
      ],
      orderList: [],
      isShow: true,
      itemNo: 0,
      pos: 0,
      order_msg: '',
      preView: 0,
      cloudPayUrl: ''
    }
  },
  created (){
    this.getorderList();
    console.log(this.movieUrl);
  },
  mounted (){
    this.newJump();
  },
  computed: {
    ...mapGetters(['authStatus'])
  },
  methods: {
    getorderList (){
       let add = this.detailData.packageId;
      // let add = acc.split("");
      // for (const key in add) {
      // let add = 's100000707';
      for (const i in this.orderLists) {
        if (add == this.orderLists[i].productid) {
          this.orderList.push(this.orderLists[i]);
        }
      }
      // }
    },
    getIsord (pos, packageId){
      let that = this;
      auth.queryBoughtCp(function (data){
        console.log(JSON.stringify(data));
        let jsonData = data;
        if (jsonData.result === 400 || jsonData.result === '400'){
          that.cloudPayUrl = jsonData.cloudPayUrl;// 云支付地址
          console.log('支付地址：' + that.cloudPayUrl);
          that.$store.commit('GET_AUTHSTATUS', false)
          that.order_msg = '订购失败'
          location.href = that.cloudPayUrl + '&backUrl=' + that.movieUrl;
          console.warn(that.cloudPayUrl + '&backUrl=' + that.movieUrl);
        } else if (jsonData.result === 0 || jsonData.result === '0'){
          that.order_msg = '订购成功'
          that.$store.commit('GET_AUTHSTATUS', true);
        }
        var logOb = {
          pkg_type: that.orderList[that.itemNo].productType,
          pkg_id: that.orderList[that.itemNo].id,
          // pkg_price: this.orderList[this.itemNo].price,
          // action: '1',
          operator_id: '',
          order_msg: that.order_msg,
          parent_page_id: c.getParentPageId(),
          parent_page_type: c.getParentPageType(),
          // preview: this.preView,
          point: that.$route.params.point
        };
        bi.order(logOb);
      }, pos, packageId);
    },
    keyCode (kc) {
      if (kc == 'down') {
        if (this.pos === 0){
          this.pos = 1;
        }
      } else if (kc == 'up') {
        if (this.pos === 1){
          this.pos = 0;
        }
      } else if (kc == 'left') {
        if (this.pos === 0){
          if (this.itemNo === 0) return;
          this.itemNo--;
        }
      } else if (kc == 'right') {
        if (this.pos === 0){
          if (this.itemNo >= (this.orderList.length - 1)) return;
          this.itemNo++;
        }
      } else if (kc == 'KeyEnter') {
        // 位置为0是支付l
        if (this.pos == 0){
          // location.href = 'http://unionpay.sca.bcs.ottcn.com:8382/moviePackage2.3.sichuan.cp/index.html?custId=9950000002388099&contentId=1&source=CP&productIds=1018000004&merchantId=SP_SJTV&businessType=VIDEO&action=danpian&backUrl=http://localhost:8079/sichuan/detail';
          this.getIsord(this.detailData.assetId, this.orderList[this.itemNo].productid);
        // 位置为1是暂不订购l
        } else if (this.pos == 1) {
          this.setLogJump();
          let routePath = this.$route.params.movieUrl.split('/')[4];
          let routePaths = routePath.split('?');
          this.$router.push({name: routePaths[0]});
        }
      } else if (kc == 'KeyBack') {
        let routePath = this.$route.params.movieUrl.split('/')[4];
        let routePaths = routePath.split('?');
        this.$router.push({name: routePaths[0]});
      }
    },
    newJump (){
      this.$nextTick(() => {
        if (c.isJump() == 'jump'){
          c.removeJump();
          let jumpObj = {
            page_type: '101',
            page_id: this.detailData.assetId,
            parent_page_type: c.getParentPageType(),
            parent_page_id: c.getParentPageId()
          }
          bi.newJump(jumpObj)
        }
      })
    },
    setLogJump (){
      c.setJump();
      c.setParentPageType('101');
      c.setParentPageId(this.detailData.assetId);
    }
  }

}
</script>



